.ac {
  display: flex;
  position: relative;
  flex-direction: column;
  background-color: var(--ac-white-color);
  height: 100vh;
  min-width: 1000px;
}

header {
  display: flex;
  align-items: center;
  background-color: var(--ac-accent-color);
  min-height: 50px;
  z-index: 1;
}

header > span.logo {
  background-image: var(--logo);
  background-repeat: no-repeat;
  background-position: 20px 50%;
  height: 100%;
  width: 6%;
}

.logo-text {
  font-weight: 600;
  font-size: 24px;
  color: white;
}
