/*
* App Viewer
*/

.table-header {
  display: flex;
  align-items: start;
}

.act {
  display: flex;
  flex-direction: column;

  .act-footer {
    height: 50px;
  }
}

/*
* Table
*/
body .p-datatable.p-component {
  /*font-family: Roboto, Helvetica, sans-serif;*/
  
  .p-datatable-thead > tr > th,
  .p-datatable-thead > tr {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding: 10px!important;
    height: 40px!important;
    > th.act-col-actions {
      text-align: center;
    }

    span.pi-sort-amount-down:before{
      content: "\E906";
    }

    span.pi-sort-amount-up-alt:before{
      content: "\E907";
    }

    span.pi-sort-alt:before{
      content: "\E915";
    }
  }

  .p-datatable-tbody {
    > tr > td > div:not(.acd) {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      font-size: 0.85rem;
    }
    
    > tr.p-datatable-row {
      cursor: pointer;
      
      > td .p-row-toggler:focus {
        box-shadow: none;
      }

      &.act-expanded-row > td {
        border-bottom: none;
      }

      &:not(.act-expanded-row) > td.act-col-actions {
        border-left: 1px solid var(--ac-secondary-bg-color);
      }

      &:hover .act-td-verdict,
      &:hover.act-expanded-row .act-td-verdict,
      &:hover.act-expanded-row + tr .acd-verdict,
      &.act-expanded-row + tr:hover .acd-verdict {
        border-color: var(--ac-accent-color);
      }
    }

    > tr:not(.p-datatable-row) > td {
      border: none;
    }

    > tr:nth-child(even) {
      background-color: var(--ac-white-color);
    }

    > tr.p-datatable-emptymessage > td {
      font-size: 0.85rem;
    }
  }
}

.act-td-verdict {
  height: 35px;
  border-left: 4px solid var(--ac-secondary-border-color);
}

.act-td-type {
  display: flex;
  align-items: center;
  width: 26px;
  
  img {
    color: var(--ac-accent-color);
    width: 16px;
    height: 16px;
    margin: 0px 5px 0px 5px;
  }
}

.act-td-name {
  display: flex;
  align-items: center;

  img {
    width: 16px;
    height: 16px;
    margin-right: 10px;

    &.loaded + img {
      display: none;
    }

    &:not(.act-icon) {
      position: absolute;
    }
  }

  span {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-bottom: 2px;
  }
}

.act-col-verdict {
  width: 25px;
}

.act-td-time {
  font-size: 0.7em;
}

.act-col-actions {
  text-align: center;

  img {
    padding: 8px;

    &:hover {
      background-color: var(--ac-hover-primary-color);
      border-radius: 5px;
    }
  }
}

/*.act-icon {
  position: absolute;
}*/

.act .p-tabview .p-tabview-panels{
  padding: 0!important;
}

.p-tabview .p-tabview-nav {
  border: none!important;
}

.p-tabview .p-tabview-nav li .p-tabview-nav-link {
  padding: 0 1rem .5rem 1rem !important;
}

.filterTypes {
  margin-right: 10px;
}

.pr-2 {
  padding-right: 5px;
}

.p-tabview .p-tabview-nav li .p-tabview-nav-link:not(.p-disabled):focus {
  box-shadow: none!important;
}

.pi-designs {
  padding: 8px;
  color: cornflowerblue;
  cursor: pointer;
}

.pi-designs:hover {
  background-color: var(--ac-hover-primary-color);
  border-radius: 5px;
}

.actions-div {
  display: flex;
  width: 92px;
  justify-content: end;
}

.designFilesLabel {
  background-color: #fff;
  border-width: 2px;
  border-style: solid;
  border-color: #0066b3;
  color: #0066b3;
  cursor: pointer;
  padding: 5px;
  margin-left: auto;
}

.p-datatable .p-datatable-thead > tr > th {
  font-weight: 600!important;
}

.uploadSection {
  width: 100%;
  display: flex;
  margin-bottom: 10px;
  margin-top: 10px;
}

.p-tabview .p-tabview-nav li.p-highlight .p-tabview-nav-link {
  background-color: #ffffff!important;
  color: #0000FF!important;
  font-size: 14px;
  font-weight: bold;
}