/* 
* App Configuration
*/
.ac {
  display: flex;
  position: relative;
  flex-direction: column;
  background-color: var(--ac-white-color);
  height: 100vh;
  min-width: 1000px;
}

header {
  display: flex;
  align-items: center;
  background-color: var(--ac-accent-color);
  min-height: 50px;
  z-index: 1;
}

header > span.logo {
  background-image: var(--logo);
  background-repeat: no-repeat;
  background-position: 20px 50%;
  height: 100%;
  width: 6%;
}

.ac-container {
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  background-color: #e7dfdf87;
}

.ac-content {
  display: flex;
  flex-direction: column;
  min-width: 625px;
  width: 100%;
}

.ac-title {
  display: flex;
  color: var(--ac-accent-color);
  font-weight: bold;
  font-size: 16px;
  margin: 0;
  background-color: white;
  padding: 8px 16px;
}

.ac-separator {
  background-color: var(--ac-secondary-bg-color);
  width: 100%;
  height: 1px;
  margin: 10px 0px 10px 0px;
}

.ac-viewer {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: white;
  margin: 10px;
  border-radius: 5px;
}

.p-tabview-title {
  font-size: 14px;
}

.act-splitbutton {
  width: 8rem;
  margin-right: 5px;
}

body .act .p-splitbutton.p-component .p-button {
  height: 2rem;
}

.ac-search-inputtext {
  height: 2rem !important;
}

body .act .p-splitbutton.p-component .p-button:first-child {
  border-right: 1px solid black !important;
}

.p-splitbutton-defaultbutton {
  border-radius: 4px 0px 0px 4px;
}
