/*
* Filters
*/
body .p-inputgroup {
    justify-content: right;

    div {
        display: flex;

        .p-inputtext {
            border-radius: 0px;
            padding-left: 2.5rem;
            border-radius: 5px;
        }
    }

    .p-inputtext.p-component.ac-search-inputtext,
    .p-inputtext.p-component.ac-search-inputtext:hover,
    .p-inputtext.p-component.ac-search-inputtext:focus {
        background-color: var(--ac-white-color);
        border: 2px solid var(--ac-primary-border-color);
        box-shadow: none;
        width: 250px;
        margin-right: 10px;
    }
}
  
.act-radiogroup {
    display: flex;
    text-align: left;
    padding-top: 1px;
    
    span {
        display: flex;
        align-items: center;
        margin-right: 20px;
    }

    label {
        color: var(--ac-primary-bg-color);
        opacity: 0.9;
        padding-left: 5px;
        margin-bottom: 0px;
        font-weight: normal;
        cursor: pointer;
    }

    .p-radiobutton .p-radiobutton-box {
        -ms-transform: scale(0.8);
        -webkit-transform: scale(0.8);
        transform: scale(0.8);
    }
}
