/*
* app form
*/
.ac-dlg {
    width: 100%;
    max-width: 800px;

    .ac-dlg-header {
        display: flex;
        align-items: center;

        img {
            width: 26px;
            height: 26px;
            margin-right: 10px;
        }
    }
    
    &.p-dialog.p-component .p-dialog-content {
        padding-top: 0;
        font-size: 0.85rem;
        overflow: hidden;
        margin-bottom: -16px;
    }

    .ac-dlg-label {
        position: relative;
        font-size: 0.85rem;
        margin-bottom: 0;
        display: inline-block;
        vertical-align: bottom;

        div {
            margin: 15px 0px 5px 0px;
        }

        span {
            display: flex;
        }

        &:focus-within {
            color: var(--ac-accent-color);
        }

        &.ac-invalid-input input {
            border: 2px solid var(--ac-danger-color);
            box-shadow: none !important;
        }

        &.ac-invalid-input + span.ac-invalid-input-msg {
            display: initial;
            color: var(--ac-danger-color); 
        }

        &.disabled {
            opacity: 0.7;
            width: auto;
        }
    }

    .ac-dlg-tooltip {
        padding: 0px 6px 2px 6px;
    }

    .ac-invalid-input-msg {
        display: none;
        font-size: 0.8rem;
    }

    .ac-required div:after {
        content: '*';
        margin-left: 5px;
        color: var(--ac-danger-color);
    }

    input, label, .p-dropdown.p-component {
        width: 100%;

        ::placeholder, .p-placeholder {
            opacity: 0.5;
        }

        ::-ms-clear {
            display: none;
        }
    }

    .p-inputtextarea.p-component {
        width: 100%;
        height: 70px;
        resize: none;
        padding-top: 10px;
        padding-bottom: 10px;
    }

    .ac-windowstype label {
        margin: 0px 40px 0px 5px;
    }

    .ac-error {
        display: none;
        align-items: center;
        height: 40px;
        margin-bottom: 10px;
        border: 1px solid var(--ac-danger-color);
        border-left: 5px solid var(--ac-danger-color);
        font-size: 0.8rem;

        &.visible {
            display: flex;
        }

        span {
            width: 18px;
            height: 18px;
            margin: 20px;
            background: url('../resources/icons/error.svg') 100% no-repeat;
        }
    }

    button[type=submit] {
        display: none;
    }
}

.ac-dropdown-badge {
    position: absolute;
    top: 48px;
    right: 35px;
    width: 24px;
    height: 24px;
    z-index: 1;

    &.running {
        background: url('../resources/icons/processing.svg') no-repeat;
        background-size: contain;
        animation: rotate 1s linear infinite;
    }
}

.p-dialog .p-dialog-header{
    background: none;
    border: none;
  }

  body .p-dialog.p-component .p-dialog-footer {
    background: none;
    border: none;
  }

.designDiv {

    label {
        font-size: 13px;
        width: 15%;
        margin-top: 1px;
    }

    .pi-plus {
        font-size: .8rem!important;
    }

    .p-inputswitch {
        width: 2.5rem;
        height: 1.2rem;
        margin-top: 2px;
    }

    .p-inputswitch .p-inputswitch-slider:before {
        width: 0.8rem;
        height: 0.8rem;
        left: 0.25rem;
        margin-top: -0.4rem;
    }
}

.editAppThumbnail{
    width:100px;
    height:80px;
    margin: 10px 10px 0;
    padding: 5px;
    border: 1px solid;
}

.d-flex {
    display: flex;
}

#preview-images {
    overflow-y: auto;
    height: 280px;
    flex-wrap: wrap;
    width: 100%;
    padding: 5px;
    position: relative;
    top: 7px;
}

.deleteImg {
    border: 0;
    position: relative;
    top: -88px;
    right: -35px;
    cursor: pointer;
    transition: 0.3s;
    background: rgb(24 24 24);
    color: white;
    border-radius: 50% 50%;
    padding: 4px 4px;
    opacity: 0;
    font-size: 8px;
}

.editAppThumbnail:hover + .deleteImg, .deleteImg:hover, .editAppThumbnail:focus-visible + .deleteImg, .deleteImg:focus-visible{
    opacity: 1;
}

.thumbnailName {
    margin: 0;
    overflow: hidden;
    text-overflow: ellipsis;
    text-wrap: nowrap;
    position: relative;
    top: -24px;
}
.img-div {
    width: 20%;
    text-align: center;
    padding: 5px;
    height: 50%;
}

.message-info-class {
    padding: 10px!important;
}

.ac-dlg {
    padding: 10px;
}

.ac-dlg .p-tabview-panels {
    overflow-y: auto;
    height: 380px;
    margin-top: 5px;
}

.justify-content-start {
    justify-content: flex-start!important;
}

.uploadInstructions {
    font-size: 12px;
    margin: 0;
  }

.uploadSection .p-panel-header {
    padding: 0px!important;
    background: white;
    border: none;
}

.uploadSection .p-panel-content {
    padding: 0;
    border: none;
}

.uploadSection .p-panel .p-panel-header .p-panel-header-icon:focus {
    box-shadow: none;
}

.validationMessage {
    color: red;
    margin: 5px 31px;
}

.editAppThumbnail.newImage {
    border: dashed;
}